// ==========================================================================
// Panels
// ==========================================================================

.panel {
    @extend %panel-default;
    @include m(centered) {
        @extend %panel-default;
        @extend %center-align;
    }

    @include m(padded) {
        @extend %panel-padding;
        @include m(centered) {
            @extend %panel-padding;
            @extend %center-align;
        }
    }
}
